import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import Breadcrumb from "../components/common/breadcrumb";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import PortfolioScreen from "../screens/matrimony-clone/portfolioscreen";
import MoveForward from "../components/common/move-forward";

const MatrimonyClone = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Matrimony Clone Script", link: null },
  ];
  return (
    <Layout db={db}>
      <Breadcrumb items={breadcrumbLinks} />
      <PortfolioScreen db={db} />
      <MoveForward db={db} />
    </Layout>
  );
};

export const Head = (props) => {
  const { data } = props;
  const fileUrl =
    data?.matrimonyCloneScriptImg?.childImageSharp?.gatsbyImageData?.images?.fallback
      ?.src;

  return (
    <Seo
      title="Matrimony Clone Script | Justtry Technologies"
      metaTitle="Matrimony Clone Script | Justtry Technologies"
      description="Explore a world of meaningful connections on our matrimony platform. Secure and user-friendly, our website facilitates intelligent matchmaking, advanced search filters, and a supportive community. "
      image={fileUrl}
    ></Seo>
  );
};

export default MatrimonyClone;

export const queryData = graphql`
  query MyQuery {
    matrimonyCloneScriptImg: file(relativePath: { eq: "matrimony-clone-script.png" }) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
